





















































































































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Emit,
  Watch
} from "vue-property-decorator";
import { settingsModule } from "@/store/modules";
import {
  cleanCronExpression,
  nextCronExecutionDates,
  datesInDistanceInWords,
  humanReadableExpression
} from "@/misc/cron";
import { Language } from "@/shared";
import VueI18n from "vue-i18n";
import { OrdinalMixin } from "@/mixins";
import JobExecutionCalendar from "@/components/misc/JobExecutionCalendar.vue";

enum RepeatTypes {
  hourly = "hourly",
  daily = "daily",
  weekly = "weekly",
  monthly = "monthly"
}

type DailyOption = "everyWeekday" | number;

enum WeeklyOption {
  mon,
  tue,
  wed,
  thu,
  fri,
  sat,
  sun
}

// Format:
// *  *  *  *  *  *
// ┬  ┬  ┬  ┬  ┬  ┬
// │  │  │  │  │  │
// │  │  │  │  │  └ day of week (0 - 7) (0 or 7 is Sun)
// │  │  │  │  └─── month (1 - 12)
// │  │  │  └────── day of month (1 - 31)
// │  │  └───────── // hour (0 - 23)
// │  └──────────── // minute (0 - 59)
// └─────────────── // second (0 - 59, optional)

@Component({
  components: {
    JobExecutionCalendar
  }
})
export default class RepeatPatternInput extends Mixins(OrdinalMixin) {
  @Prop({})
  value?: string;

  @Prop({
    type: String,
    default: ""
  })
  label!: string;

  showCalendar = false;
  currentRepeatType: RepeatTypes | null = null;

  get hourlySlider(): number {
    const hourlyPattern = /\*\/(\d+)/;
    if (hourlyPattern.test(this.cronExpressionArr[2])) {
      const match = hourlyPattern.exec(this.cronExpressionArr[2]);
      if (match) {
        return Number(match[1]);
      }
    }
    return 1;
  }

  set hourlySlider(val) {
    const newArr = [...this.cronExpressionArr];

    newArr[2] = `*/${val}`;

    this.cronExpressionArr = newArr;
  }

  get monthlyDayModel(): number[] {
    const days = this.cronExpressionArr[3].split(",");
    if (days.length === 1 && days[0] === "*") {
      return new Array(31).fill(null).map((_, idx) => idx + 1);
    }
    return days.map(day => Number(day));
  }

  set monthlyDayModel(val) {
    const newArr = [...this.cronExpressionArr];

    val = val.filter(n => !isNaN(n));

    if (val.length === 31) {
      newArr[3] = "*";
    } else if (val.length) {
      newArr[3] = val.sort((a, b) => a - b).join(",");
    } else {
      // no day is not possible
      newArr[3] = "*";
      this.$nextTick(() => {
        newArr[3] = "1";
        this.cronExpressionArr = newArr;
      });
    }

    this.cronExpressionArr = newArr;
  }

  @Watch("currentRepeatType")
  onRepeatTypeChange() {
    switch (this.currentRepeatType) {
      case RepeatTypes.daily:
        // clear months
        this.cronExpressionArr = this.cronExpressionArr.map(
          (originalVal, idx) =>
            idx === 4
              ? "*"
              : idx === 5 && originalVal !== "MON-FRI"
              ? "*"
              : idx === 2
              ? "0"
              : originalVal
        );
        break;
      case RepeatTypes.weekly:
        // clear days
        this.cronExpressionArr = this.cronExpressionArr.map(
          (originalVal, idx) =>
            idx === 3 ? "*" : idx === 2 ? "0" : originalVal
        );
        break;
      case RepeatTypes.monthly:
        // clear weekdays
        this.cronExpressionArr = this.cronExpressionArr.map(
          (originalVal, idx) =>
            idx === 5 ? "*" : idx === 2 ? "0" : originalVal
        );
        break;
      case RepeatTypes.hourly:
        this.cronExpressionArr = this.cronExpressionArr.map(
          (originalVal, idx) =>
            idx === 2
              ? "*/5"
              : idx === 1
              ? "0"
              : idx === 5 || idx === 3 || idx === 4
              ? "*"
              : originalVal
        );
        break;
    }
  }

  get timePickerModel() {
    return `${this.cronExpressionArr[2]}:${this.cronExpressionArr[1]}`;
  }

  set timePickerModel(val) {
    const [hour, minute] = val.split(":").map(numStr => String(Number(numStr))); // remove leading 0s
    this.cronExpressionArr = this.cronExpressionArr.map((originalVal, idx) =>
      idx === 1 ? minute : idx === 2 ? hour : originalVal
    );
  }

  get weeklyRepeatModel(): WeeklyOption[] {
    const options: WeeklyOption[] = [];
    const currentDays = this.cronExpressionArr[5].split(",");
    if (currentDays.includes("*")) {
      options.push(
        WeeklyOption.mon,
        WeeklyOption.tue,
        WeeklyOption.wed,
        WeeklyOption.thu,
        WeeklyOption.fri,
        WeeklyOption.sat,
        WeeklyOption.sun
      );
    }
    if (currentDays.includes("MON-FRI")) {
      options.push(
        WeeklyOption.mon,
        WeeklyOption.tue,
        WeeklyOption.wed,
        WeeklyOption.thu,
        WeeklyOption.fri
      );
    }
    if (currentDays.includes("MON")) {
      options.push(WeeklyOption.mon);
    }
    if (currentDays.includes("TUE")) {
      options.push(WeeklyOption.tue);
    }
    if (currentDays.includes("WED")) {
      options.push(WeeklyOption.wed);
    }
    if (currentDays.includes("THU")) {
      options.push(WeeklyOption.thu);
    }
    if (currentDays.includes("FRI")) {
      options.push(WeeklyOption.fri);
    }
    if (currentDays.includes("SAT")) {
      options.push(WeeklyOption.sat);
    }
    if (currentDays.includes("SUN")) {
      options.push(WeeklyOption.sun);
    }
    return options;
  }

  set weeklyRepeatModel(val) {
    const arr = [...this.cronExpressionArr];

    const days = [];

    if (
      val.includes(WeeklyOption.mon) &&
      val.includes(WeeklyOption.tue) &&
      val.includes(WeeklyOption.wed) &&
      val.includes(WeeklyOption.thu) &&
      val.includes(WeeklyOption.fri) &&
      val.length === 5
    ) {
      arr[5] = "MON-FRI";
    } else {
      if (val.includes(WeeklyOption.mon)) days.push("MON");
      if (val.includes(WeeklyOption.tue)) days.push("TUE");
      if (val.includes(WeeklyOption.wed)) days.push("WED");
      if (val.includes(WeeklyOption.thu)) days.push("THU");
      if (val.includes(WeeklyOption.fri)) days.push("FRI");
      if (val.includes(WeeklyOption.sat)) days.push("SAT");
      if (val.includes(WeeklyOption.sun)) days.push("SUN");
      if (days.length) {
        arr[5] = days.join(",");
      } else {
        // this expression would be invalid
        arr[5] = "*";
        this.$nextTick(() => {
          arr[5] = "MON";
          this.cronExpressionArr = arr;
        });
      }
    }
    this.cronExpressionArr = arr;
  }

  get dailyRepeatModel(): DailyOption | null {
    const dailyPattern = /\*\/(\d+)/;
    if (
      this.cronExpressionArr[3] === "*" &&
      this.cronExpressionArr[5] === "*"
    ) {
      return 1;
    }
    if (
      dailyPattern.test(this.cronExpressionArr[3]) &&
      this.cronExpressionArr[5] === "*"
    ) {
      const match = dailyPattern.exec(this.cronExpressionArr[3]);
      if (match) {
        return Number(match[1]);
      }
    }
    if (
      this.cronExpressionArr[3] === "*" &&
      this.cronExpressionArr[5] === "MON-FRI"
    ) {
      return "everyWeekday";
    }
    return null;
  }

  set dailyRepeatModel(val) {
    if (!val) return;
    const newArr = [...this.cronExpressionArr];
    if (val === "everyWeekday") {
      newArr[3] = "*";
      newArr[5] = "MON-FRI";
    } else {
      newArr[3] = val === 1 ? "*" : `*/${val}`;
      newArr[5] = "*";
    }
    this.cronExpressionArr = newArr;
  }

  get monthlyRepeatModel(): number | null {
    const monthlyPattern = /\*\/(\d+)/;
    if (this.cronExpressionArr[4] === "*") {
      return 1;
    }
    if (monthlyPattern.test(this.cronExpressionArr[4])) {
      const match = monthlyPattern.exec(this.cronExpressionArr[4]);
      if (match) {
        return Number(match[1]);
      }
    }
    return null;
  }

  set monthlyRepeatModel(val) {
    if (!val) return;
    const newArr = [...this.cronExpressionArr];

    newArr[4] = val === 1 ? "*" : `*/${val}`;
    this.cronExpressionArr = newArr;
  }

  get weeklyItems(): {
    text: string | VueI18n.LocaleMessage;
    value: WeeklyOption;
  }[] {
    return [
      {
        text: this.$t("scheduling.weekly.monday"),
        value: WeeklyOption.mon
      },
      {
        text: this.$t("scheduling.weekly.tuesday"),
        value: WeeklyOption.tue
      },
      {
        text: this.$t("scheduling.weekly.wednesday"),
        value: WeeklyOption.wed
      },
      {
        text: this.$t("scheduling.weekly.thursday"),
        value: WeeklyOption.thu
      },
      {
        text: this.$t("scheduling.weekly.friday"),
        value: WeeklyOption.fri
      },
      {
        text: this.$t("scheduling.weekly.saturday"),
        value: WeeklyOption.sat
      },
      {
        text: this.$t("scheduling.weekly.sunday"),
        value: WeeklyOption.sun
      }
    ];
  }

  get dailyItems(): {
    text: string | VueI18n.LocaleMessage;
    value: DailyOption;
  }[] {
    return [
      {
        text: this.$t("scheduling.daily.every-weekday"),
        value: "everyWeekday"
      },
      ...new Array(20).fill(null).map((_, idx) => ({
        text: this.$tc("scheduling.daily.every-n-day", idx + 1),
        value: idx + 1
      }))
    ];
  }

  get monthlyDayItems(): {
    text: string | VueI18n.LocaleMessage;
    value: number;
  }[] {
    return new Array(31).fill(null).map((_, idx) => ({
      text: this.$t("scheduling.monthly.on-the-nth-day", {
        n: this.ordinal(idx + 1)
      }),
      value: idx + 1
    }));
  }

  get monthlyRepeatItems(): {
    text: string | VueI18n.LocaleMessage;
    value: number;
  }[] {
    return new Array(12).fill(null).map((_, idx) => ({
      text: this.$tc("scheduling.monthly.every-n-month", idx + 1),
      value: idx + 1
    }));
  }

  @Watch("value", { immediate: true })
  onExpressionChange() {}

  @Emit("input")
  setValue(val: string) {}

  get timePickerFormat() {
    return settingsModule.language === Language.de ? "24hr" : "ampm";
  }

  set cronExpressionArr(val) {
    this.cronExpression = val.join(" ");
  }

  get cronExpressionArr() {
    return this.cronExpression.split(" ");
  }

  set cronExpression(val: string) {
    this.setValue(val);
  }

  get cronExpression() {
    return cleanCronExpression(this.value);
  }

  get repeatTypesValues() {
    return RepeatTypes;
  }

  get nextExecutionDates() {
    return datesInDistanceInWords(
      nextCronExecutionDates(this.cronExpression, 10)
    ).join(", ");
  }

  get humanReadableExpression() {
    const readable = humanReadableExpression(this.cronExpression);
    if (!readable) {
      return this.$t("scheduling.cron.invalid-expression");
    }
    return readable;
  }

  get repeatTypes() {
    return [
      {
        text: this.$t("scheduling.repeat-type.hourly"),
        value: RepeatTypes.hourly
      },
      {
        text: this.$t("scheduling.repeat-type.daily"),
        value: RepeatTypes.daily
      },
      {
        text: this.$t("scheduling.repeat-type.weekly"),
        value: RepeatTypes.weekly
      },
      {
        text: this.$t("scheduling.repeat-type.monthly"),
        value: RepeatTypes.monthly
      }
    ];
  }

  clearCurrentRepeatType() {
    this.currentRepeatType = null;
  }
}
